.con{
  padding: 90px 0 100px;
  background: #f3f5f8;
}
.list{
  width: 940px;
  height: 200px;
  margin: 70px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}
.contactItem{
  width: 30%;
  transition: all 500ms ease-in-out;
  background: #fff;

  &:hover{
    transform: translate(0,-15px);
    box-shadow: 0px 3px 0px 0px #3dc9b3;
  }
}
.contactName{
  letter-spacing: 1px;
  font-size: 14px;
  margin-top: 15px;
  color: #3dc9b3;
  text-align: center;

  //&:after{
  //  content: "\020";
  //  border: solid 2px #3dc9b3;
  //  display: block;
  //  width: 35px;
  //  margin: 25px auto;
  //}
}
.contactNameLarge{
  &:after{
    content: "\020";
    border: solid 2px #3dc9b3;
    display: block;
    width: 35px;
    margin: 25px auto;
  }
}
.contactValue{
  margin: 15px auto;
  color: #8c9398;
  word-spacing: 1px;
  line-height: 25px;
  font-size: 14px;
}
.wxImage{
  display: block;
  width: 150px;
  height: 150px;
  border: none;
  margin: 15px auto;
}
.kefuImage{
  display: block;
  width: 150px;
  border: none;
  margin: 0 auto;
}
