.con{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  background: url(./banner2.jpg) no-repeat scroll 0 0;
  background-size: cover;
}
.title{
  font-size: 40px;
  letter-spacing: 12px;
  margin: 10px;
  color: #353535;
}
.titleHighlight{
  color: #3dc9b3;
}
.desc{
  color: #8c9398;
  word-spacing: 1px;
  line-height: 25px;
  font-size: 14px;
}
