.con{
  background: #fff;
}
.body{
  width: 1080px;
  height: 440px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}
.item{
  width: 30%;
}
.itemTitle{
  font-size: 15px;
  color: #353535;
  font-weight: bold;

  &:after{
    content: "\020";
    border: solid 2px #3dc9b3;
    display: block;
    width: 35px;
    margin: 25px auto;
  }
}
.itemDesc{
  color: #8c9398;
  word-spacing: 1px;
  line-height: 25px;
  font-size: 14px;
}
